.nav
{   
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 60px;
    width: 100%;
    background-color: #323330;
}

.list
{
    display: flex;
    width: 450px;
    padding-right: 60px;
    justify-content: space-between;
}

.item
{
    color: #ffffff;
    text-decoration: none;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.item:hover
{
    color: #f0db4f;
}